<template>
  <div class="sidebar">
    <img
      src="@/assets/logo_seu-artista.svg"
      alt="Seu Artista Logo"
      class="logo"
    />
    <div class="navs">
      <router-link
        :to="nav.path"
        class="nav"
        v-for="(nav, index) in navs"
        :key="index"
      >
        <img :src="nav.icon" :alt="nav.name + ' icon'" />
        <span>{{ nav.name }}</span>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "Sidebar",
  data() {
    return {
      navs: [
        {
          name: "Dashboard",
          path: "/admin/dashboard",
          icon: this.getImgUrl("ic_dashboard"),
          roles: [],
        },
        {
          name: "Agenda",
          path: "/admin/agenda",
          icon: this.getImgUrl("ic_agenda"),
          roles: [],
        },
        {
          name: "Contratos",
          path: "/admin/contracts",
          icon: this.getImgUrl("ic_contracts"),
          roles: [],
        },
        {
          name: "Pagamentos",
          path: "/admin/payments",
          icon: this.getImgUrl("ic_payments"),
          roles: [],
        },
        {
          name: "Perfil",
          path: "/admin/profile",
          icon: this.getImgUrl("ic_profile"),
          roles: [],
        },
      ],
    };
  },
  methods: {
    getImgUrl(icon) {
      var images = require.context("@/assets/", false, /\.svg$/);
      return images("./" + icon + ".svg");
    },
  },
};
</script>

<style lang="scss" scoped>
.sidebar {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  .logo {
    margin-bottom: 25px;
    margin-left: 10px;
  }
  .navs {
    display: flex;
    flex-direction: column;

    a {
      text-decoration: none;
      padding: 15px 30px 15px 10px;
      border-radius: 4px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin: 10px 0;
      min-width: 200px;

      &.router-link-active {
        background-color: $footer-grey;
      }

      img {
        margin-right: 10px;
      }

      span {
        font-size: 1.8rem;
        text-decoration: none;
        color: $white;
      }
    }
  }
}
</style>
