import Vue from "vue";
import VueRouter from "vue-router";
import Content from "@/views/Content";
import Home from "@/views/Home";
import Artist from "@/views/Artist";
import HowDoesItWork from "@/views/HowDoesItWork";
import SignIn from "@/views/SignIn";
import ForArtists from "@/views/ForArtists";
import Artists from "@/components/Main/Artists/Artists";
import ArtistDetails from "@/components/Main/Artists/ArtistDetails";

import Admin from "@/views/Admin";
import AdminContent from "@/views/AdminContent";
import Login from "@/components/Admin/Login";
import Profile from "@/components/Admin/Profile";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: Content,
    redirect: "/home",
    children: [
      {
        path: "/home",
        name: "Home",
        component: Home,
      },
      {
        path: "/hdiw",
        name: "HDIW",
        component: HowDoesItWork,
      },
      {
        path: "/forartists",
        name: "ForArtists",
        component: ForArtists,
      },
      {
        path: "/signin",
        name: "SignIn",
        component: SignIn,
      },
      {
        path: "/artists",
        name: "ArtistsListing",
        redirect: "/artists/listing",
        component: Artist,
        children: [
          {
            path: "listing",
            name: "Artists",
            component: Artists,
          },
          {
            path: ":artistId",
            name: "ArtistsDetails",
            component: ArtistDetails,
          },
        ],
      },
    ],
  },
  {
    path: "/admin",
    component: Admin,
    children: [
      {
        path: "/",
        component: AdminContent,
        children: [
          {
            path: "profile",
            component: Profile,
            name: "Profile",
          },
        ],
      },
      {
        path: "/login",
        component: Login,
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
